header {
    min-height: 60px;
    background: $grey;
    display: flex; }
.logo-container {
    width: 35%;
    @include flex(center, center); }
.menu-container {
    width: 30%;
    @include flex(center, around); }
.search-container {
    width: 15%;
    @include flex(center, around); }
.user-container {
    width: 20%;
    @include flex(center, around); }
.logo {
    @include link(white);
    width: 96px;
    height: 29px; }
.menu-button {
    width: 33%; }
header a {
    @include link($light-grey);
    @include text-settings(16px, $light-grey); }
header a:hover {
    @include link(white); }
.arrow {
    @include bg;
    width: 12px;
    height: 7px;
    margin-left: 8px;
    background-image: url(./images/Shape.png); }
.search {
    width: 100%;
    height: 38px;
    border: 1px solid #CCCCCC;
    border-radius: 4px; }
.search input {
    border: none;
    width: 100%;
    height: 100%;
    padding-left: 10px; }
.search input::placeholder {
    padding-left: 15px;
    @include text-settings(16px, #435766); }
.user-icon {
    @include bg;
    width: 20px;
    height: 20px;
    margin-left: 29px;
    margin-right: 5px;
    background-image: url(./images/user-icon.png); }


