@import "./reset.css";
@import url('https://fonts.googleapis.com/css2?family=Heebo&display=swap') ;
$green: #8DD83D;
$grey: #373A3C;
$light-grey: rgba(255, 255, 255, 0.5);
@mixin flex($ai, $jc) {
    display: flex;
    align-items: $ai;
    justify-content: $jc; }
@mixin text-settings($fsize, $color) {
    font-size: $fsize;
    color: $color;
    font-family: 'Heebo', sans-serif; }
@mixin bg {
    background-position: center;
    background-repeat: no-repeat;
    display: inline-block; }
@mixin link($color) {
    text-decoration: none;
    text-transform: none;
    border: none;
    outline: none;
    color: $color; }
html {
    box-sizing: border-box; }
*, *:before, *:after {
    box-sizing: inherit; }
body {
    overflow-x: hidden;
    font-style: normal; }
.wrapper {
    margin: 0;
    width: 100%;
    min-height: 500px;
    @include flex(center, initial);
    flex-direction: column;
    background:#777777 {} }
.wrapper a:focus {
    text-decoration: none;
    text-transform: none;
    border: none;
    outline: none; }
.inline-wrapper {
    width: 1400px; }
header, main, footer, section {
    width: 100%; }
main, section, footer {
    min-height: 400px; }
@import "../header/header.sass";
@import "../main/main.sass";
@import "../footer/footer.sass";
